import React from 'react';
import PropTypes from 'prop-types';

// import {HandySvg} from 'handy-svg';
// import Path from 'react-svg-path';
import { Header, Gallery, Group, Div, Panel, PanelHeader} from '@vkontakte/vkui';
import './style.css'
import './Photo.css'
import camera from '../img/camera.png';
import background from '../img/fonk.png';

const Home = ({id, friends}) => (
	<Panel id={id} friends = {friends} style={{ 
		backgroundImage: `url(${background})` 
	  }}>
		{/* {()=>{
				if (friends[0] != undefined)
				console.log(friends[id].photo_200_orig)
			}
		}	 */}
		<Group header={<Header mode="secondary">ТОТ САМЫЙ ХАКАТОН</Header>}>
      		<Gallery
        		slideWidth="100%"
        		style={{ height: 700 }}
        		bullets="dark"
        		showArrows
				
      		>	
        <div>
		<Panel className="body">
			<Panel className="items_container">
				<Panel className="photo_container">
					<Panel className="photo">
						<Panel className="photo_button">
							<img className="camera_icon" src={friends[0]?friends[0].photo_200_orig:camera} alt="CameraPic"/>
						</Panel>
					</Panel>
				</Panel>
			</Panel>
		</Panel>
		</div>
		<div>
		<Panel className="body">
			<Panel className="items_container">
				<Panel className="photo_container">
					<Panel className="photo">
						<Panel className="photo_button">
							<img className="camera_icon" src={friends[1]?friends[1].photo_200_orig:camera} alt="CameraPic"/>
						</Panel>
					</Panel>
				</Panel>
			</Panel>
		</Panel>
		</div>
		<div>
		<Panel className="body">
			<Panel className="items_container">
				<Panel className="photo_container">
					<Panel className="photo">
						<Panel className="photo_button">
							<Panel className="camera_button_text">Add your photo</Panel>
							<img className="camera_icon" src={camera} alt="CameraPic"/>
						</Panel>
					</Panel>
				</Panel>
			</Panel>
		</Panel>
		</div>
      </Gallery>
    </Group>
	<Group>

	</Group>
		{/* <PanelHeader>Example</PanelHeader> */}
		{/* {fetchedUser &&
		<Group header={<Header mode="secondary">User Data Fetched with VK Bridge</Header>}>
			<Cell
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</Cell>
		</Group>} */}

		{/* <Group header={<Header mode="secondary">Navigation Example</Header>}>
			<Div>
				<Button stretched size="l" mode="secondary" onClick={go} data-to="persik">
					Show me the Persik, please
				</Button>
			</Div>
		</Group> */}
		
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	// go: PropTypes.func.isRequired,
	// fetchedUser: PropTypes.shape({
	// 	photo_200: PropTypes.string,
	// 	first_name: PropTypes.string,
	// 	last_name: PropTypes.string,
	// 	city: PropTypes.shape({
	// 		title: PropTypes.string,
	// 	}),
	// }),
};

export default Home;
